// @import "payment.scss";

.homepage{
    display: flex;
    justify-content: space-between;

    &-right{
        background: #1F1D2B;
        border-radius: 8px;
        height: 100vh;
        width: 36%;
        box-sizing: border-box;
        padding: 24px;
        box-sizing: border-box;
        // overflow-y: scroll;
        position: relative;

        .absolute{
            border-top: 1px solid #393C49;
            padding: 24px;
            position: fixed;
            background: #1F1D2B;
            box-sizing: border-box;
            bottom: 0;
            right: 23px;
            width: 33%;
            margin: auto;

            &-top{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 20px;

                &__title{
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #ABBBC2;
                }

                &__price{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 140%;
                    text-align: right;
                    color: #FFFFFF;
                }
            }

            &-center{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 45px;

                &__title{
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #ABBBC2;
                }

                &__price{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 140%;
                    text-align: right;
                    color: #FFFFFF;
                }
            }

            &-btn{
                display: block;
                width: 100%;
                background: #EA7C69;
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.3);
                border-radius: 8px;
                padding: 14px 116px;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #FAFAFA;
                outline: none;
                border: none;
                cursor: grab;

                &:active{
                    cursor: grabbing;
                }
            }
        }
        
        &-top{
            .order-number{
                font-style: normal;
                font-weight: 600;
                font-size: 20px;
                line-height: 140%;
                margin-bottom: 24px;
                color: #FFFFFF;
            }

            .servise{
                display: flex;
                align-items: center;
                margin-bottom: 24px;

                &-btn{
                    background: #1F1D2B;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    outline: none;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    padding: 7px 12px;
                    line-height: 140%;
                    color: #EA7C69;
                    margin-right: 8px;
                    cursor: grab;

                    &:last-child{
                        margin-right: 0;
                    }

                    &.red{
                        background: #EA7C69;
                        border-radius: 8px;
                        color: #fff;
                    }
                }
            }
        }

        .info-block{
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 1px solid #393C49;
            padding-bottom: 24px;
            margin-bottom: 48px;

            &-title{
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 140%;
                color: #FFFFFF;

                &:nth-child(1) {
                    margin-right: 75px;
                }
            }

            &-right{
                display: flex;
            }
        }
    }

    &-left{
        width: 64%;
        padding-top: 24px;
        padding-right: 24px;
        padding-bottom: 24px;
        box-sizing: border-box;
        // overflow-y: scroll;
        height: 100vh;
        // &::-webkit-scrollbar {
        //     width: 0 !important;
        //     height: 0;
        //     display: none !important;
        //     background: transparent;
        // }

        &-top{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 24px;

            &-right{
                width: 235px;
                padding: 14px 14px 14px 42px;
                background: #2D303E;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                position: relative;

                &::before{
                    content: '';
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 3px;
                    background-image: url(../assets/images/icons/Base.png);
                    background-repeat: no-repeat;
                    width: 40px;
                    margin: auto;
                    height: 40px;
                    z-index: 34;
                    background-size: contain;
                }

                .input{
                    background-color: #2D303E;
                    border: none;
                    color: #fff;
                    outline: none;
                    font-size: 14px;

                    &:focus .homepage-left-top-right{
                        border: 1px solid #fff;
                        box-shadow: 0 0 10px 2px #fff;
                    }
                    
                }
            }
        }

        &-center{
            margin-bottom: 34px;
            &-nav{
                display: flex;
                align-items: center;
                border-bottom: 2px solid #393C49;

                &-links{
                    margin-right: 36px;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 140%;
                    color: #FFFFFF;
                    padding-bottom: 10px;
                    transform: translateY(2px);
                    border-radius: 18px 0 0;
                    cursor: grab;
                    &.clicked{
                        color: #EA7C69;
                        border-bottom: 3px solid #EA7C69;
                        
                    }
                }
            }
        }

        &-main{
            
            &-top{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 20px;

                &-left{
                    &__title{
                        font-style: normal;
                        font-weight: 600;
                        font-size: 20px;
                        line-height: 140%;
                        color: #FFFFFF;
                    }
                }

                &-right{
                    &__select{
                        padding: 15px;
                        background: #1F1D2B;
                        border: 1px solid #393C49;
                        box-sizing: border-box;
                        border-radius: 8px;
                        color: #fff;
                        outline: none;
                    }
                }
            }

            .dish-block{
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-wrap: wrap;
                height: 550px;
                padding-top: 90px;
                overflow-y: scroll;
            &::-webkit-scrollbar {
                width: 0 !important;
                height: 0;
                display: none !important;
                background: transparent;
            }
            }
        }
    }
}

.order-block{
    height: 600px;
    overflow-y: scroll;
    &::-webkit-scrollbar {
        width: 0 !important;
        height: 0;
        display: none !important;
        background: transparent;
    }
}

.graph{
    padding: 24px;
    padding-right: 0;
    // width: 90vw;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;

    &-left{
        // width: 55vw;
        width: 65%;
        &-top{
            border-bottom: 1px solid #393C49;
            padding-bottom: 24px;
            margin-bottom: 24px;
        }
        .price-card-block{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 24px;
        }

        .customer-list{
            height: 400px;
            overflow-y: scroll;
            &::-webkit-scrollbar {
                width: 0 !important;
                height: 0;
                display: none !important;
                background: transparent;
            }
        }

        .order-report{
            background: #1F1D2B;
            border-radius: 8px;
            // height: 600px;
            padding: 33px 16px 16px 24px;
            box-sizing: border-box;
            // margin-bottom: 30px;

            &-top{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 24px;

                .order-settings{
                    padding: 14px 14px 14px 44px;
                    background: #1F1D2B;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    position: relative;
                    cursor: pointer;

                    &::before{
                        content: '';
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        margin: auto;
                        left: 14px;
                        width: 20px;
                        height: 20px;
                        background-repeat: no-repeat;
                        background-image: url(../assets/images/icons/filter.svg);
                    }

                    .order-title{
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 130%;
                        color: #FFFFFF;
                        // position: relative
                    }
                }
            }

            .menu-block{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding-bottom: 24px;
                margin-bottom: 18px;
                border-bottom: 1px solid #393C49;

                &__title{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 140%;
                    color: #FFFFFF;

                    &:last-child{
                        margin-right: 74px;
                    }
                }
            }
        }
    }

    &-right{
        // width: 35vw;
        width: 35%;
        padding: 24px;
        box-sizing: border-box;

        &-top{
            background: #1F1D2B;
            border-radius: 8px;
            padding: 32px 24px 24px 24px;
            margin-bottom: 24px;
            
            .most-ordered{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding-bottom: 24px;
                border-bottom: 1px solid #393C49;
                margin-bottom: 17px;


                &__title{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 140%;
                    color: #FFFFFF;
                }
            }

            .view{
                display: block;
                width: 100%;
                padding: 14px;
                border: 1px solid #EA7C69;
                box-sizing: border-box;
                box-sizing: border-box;
                border-radius: 8px;
                background-color: transparent;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #EA7C69;
                outline: none;
                cursor: grab;
            }
        }

        .graph-right-bottom{
            background: #1F1D2B;
            border-radius: 8px;
            padding: 24px;
            box-sizing: border-box;

            .most-ordered{
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding-bottom: 24px;
                border-bottom: 1px solid #393C49;
                margin-bottom: 17px;

                &__title{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 140%;
                    color: #FFFFFF;
                }
            }
        }

        .statistika{
            display: flex;
            // justify-content: space-between;
            align-items: center;

            &-left{
                margin-right: 50px;
            }

            .main-statis{
                display: flex;
                margin-bottom: 18px;

                &:last-child{
                    margin-bottom: 0;
                }

                .color{
                    background: #FF7CA3;
                    width: 20px;
                    height: 20px;
                    border-radius: 50%;
                    margin-right: 10px;

                    &.orange{
                        background: #FFB572;
                    }

                    &.blue{
                        background: #65B0F6;
                    }
                }

                .statis-block{
                    .statis__title{
                        font-style: normal;
                        font-weight: 500;
                        font-size: 18px;
                        line-height: 130%;
                        color: #FFFFFF;
                        margin-bottom: 4px;
                    }

                    .statis__text{
                        font-style: normal;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 140%;
                        color: #ABBBC2;
                    }
                }
            }
        }
    }
}

.title-holder{
    padding: 24px;
    padding-bottom: 19px;
}

.settings{
    padding: 24px;
    display: flex;
    justify-content: space-between;

    .settings-left{
        width: 20%;
        .settings-main{
            height: 773px;
            background: #1F1D2B;
            border-radius: 8px;
            // width: 275px;
            width: 100%;
            padding: 24px;
            box-sizing: border-box;
        }
    }

    .settings-right{
        width: 70%;
        background: #1F1D2B;
        border-radius: 8px;

        padding: 24px;
        box-sizing: border-box;

        .product{
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 34px;

            &-right{
                padding: 14px 16px 14px 44px;
                border: 1px solid #393C49;
                box-sizing: border-box;
                filter: drop-shadow(0px 8px 24px rgba(146, 136, 224, 0.3));
                border-radius: 8px;
                position: relative;

                &::before{
                    content: '';
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    left: 14px;
                    width: 20px;
                    height: 20px;
                    background-repeat: no-repeat;
                    background-image: url(../assets/images/icons/filter.svg);
                }

                &__title{
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 140%;
                    color: #FFFFFF;
                }
            }
        }
    }

    .edit-block-wrapper{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        height: 500px;
        overflow-y: scroll;
        &::-webkit-scrollbar {
            width: 0 !important;
            height: 0;
            display: none !important;
            background: transparent;
        }
        .edit-block{
            background: #1F1D2B;
            border: 1px dashed #EA7C69;
            border-radius: 8px;
            width: 221px;
            height: 299px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            cursor: grab;
    
            &__img{
                margin-bottom: 20px;
            }
    
            &__title{
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 140%;
                text-align: center;
                color: #EA7C69;
            }
        }
    }

    .buttons{
        display: flex;
        align-items: center;
        margin-top: 20px;

        .cancel{
            border: none;
            border: 1px solid #EA7C69;
            box-sizing: border-box;
            border-radius: 8px;
            padding: 14px 67px;
            background: #1F1D2B;
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #EA7C69;
            margin-right: 20px;
            outline: none;
            cursor: grab;
        }

        .confirm{
            background: #EA7C69;
            box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
            border-radius: 8px;
            padding: 14px 34px;
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #FAFAFA;
            border: none;
            outline: none;
            cursor: grab;
        }
    }
}