.absolute{
    border-top: 1px solid #393C49;
    padding: 24px;
    position: fixed;
    background: #1F1D2B;
    box-sizing: border-box;
    bottom: 0;
    right: 23px;
    width: 33%;
    margin: auto;

    &-top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;

        &__title{
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #ABBBC2;
        }

        &__price{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            text-align: right;
            color: #FFFFFF;
        }
    }

    &-center{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 45px;

        &__title{
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #ABBBC2;
        }

        &__price{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            text-align: right;
            color: #FFFFFF;
        }
    }

    &-btn{
        display: block;
        width: 100%;
        background: #EA7C69;
        box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.3);
        border-radius: 8px;
        padding: 14px 116px;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 140%;
        color: #FAFAFA;
        outline: none;
        border: none;
        cursor: grab;

        &:active{
            cursor: grabbing;
        }
    }
}