.order-note{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    &-left{
        width: 540px;

        .order-input{
            width: 100%;
            background: #2D303E;
            border: 1px solid #393C49;
            box-sizing: border-box;
            border-radius: 8px;
            padding: 14px;
            box-sizing: border-box;
            outline: none;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #E0E6E9;
        }

        .number-food{
            background: #2D303E;
            border: 1px solid #393C49;
            box-sizing: border-box;
            padding: 14px 20px;
            border-radius: 8px;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: #FFFFFF;
        }

        .alone{
            display: flex;
            align-items: center;
        }

        &-top{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 10px;

            .food-img{
                width: 42px;
                height: 42px;
                border-radius: 50%;
                margin-right: 10px;
            }

            .food-order-info{
                .food-name{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    color: #FFFFFF;
                    margin-bottom: 10px;
                }

                .food-price{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 12px;
                    line-height: 140%;
                    color: #ABBBC2;
                }
            }
        }
    }

    &-right{
        .max-price{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            text-align: right;
            color: #FFFFFF;
            margin-bottom: 40px;
        }

        .delete{
            border: 2px solid #EA7C69;
            box-sizing: border-box;
            border-radius: 8px;
            padding: 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: grab;
        }
    }
}