.sidebar-btn{
    background-color: transparent;
    border: none;
    outline: none;
    cursor: grab;
    padding: 18px;
    box-sizing: border-box;

    .orange{
        padding: 0px 7px 12px 7px;
    }

    &.active{
        background: #252836;
        border-radius: 12px 0px 0px 12px;

        .orange{
            background: #EA7C69;
            box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.32);
            border-radius: 8px;
            padding: 18px;
            display: flex;
            justify-content: center;
            align-items: center;
        
            svg{
                fill: #fff;
                position: relative;
                z-index: 3;
                object-fit: cover;
                height: 40px;
                margin-top: -3px;
            }
        }
    }

    svg{
        width: 30px;
        height: 25px;
        margin: 17px 0;
        fill: #EA7C69;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &:active{
        cursor: grabbing;
    }
}