.sidebar{
    padding: 24px 0 24px 24px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    height: 100%;
    padding-right: 10px;

    &:last-child{
        padding-bottom: 0;
    }

    .logo-holder{
        display: inline-block;

        &-link{
            display: block;

            img{
                width: 56px;
                height: 56px;
                margin: 0 auto;
                padding: 7px;
                margin-right: 20px;
            }
        }
    }
}