.customer-info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 28px;

    &:last-child{
        margin-bottom: 0;
    }

    .customer-menu_title{
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        color: #E0E6E9;
        width: 120px;
    }

    .customer-menu{
        width: 25%;
    }

    .customer{
        width: 25%;
        display: flex;
        align-items: center;

        &-img{
            width: 32px;
            height: 32px;
            margin-right: 16px;
        }

        &-name{
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 140%;
            color: #E0E6E9;
        }
    }

    .customer-payment{
        width: 25%;

        &__title{
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 140%;
            color: #E0E6E9;
        }
    }

    .customer-status{
        width: 100px;
        padding: 4px 12px;
        background: rgba(107, 226, 190, 0.24);
        border-radius: 30px;
        text-align: center;
        margin-right: 20px;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #50D1AA;

        &.preparing{
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            text-align: center;
            color: #9290FE;
            background: rgba(146, 144, 254, 0.2);
        }

        &.pending{
            color: #FFB572;
            background: rgba(255, 181, 114, 0.2);
        }
    }
}