.payment{
    background: #1F1D2B;
    border-radius: 16px 16px 0px 16px;
    // height: 100vh;
    height: 0;
    overflow: hidden;
    width: 0;
    transition: all ease 0.5s;
    // height: 100%;

    &-top{
        padding-bottom: 24px;
        border-bottom: 1px solid #393C49;
    }

    .cards{
        display: flex;
        align-items: center;
        margin-bottom: 16px;
    }

    input:checked+.label .label__title{
        color: #fff;
    }

    input:checked+.label::after{
        display: block;
    }

    input:checked+.label{
        border: 1px solid #fff;
        cursor: grabbing;
    }

    input:checked+.label svg{
        fill: #fff;
    }

    .label{
        padding: 13px 20px 10px 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px solid #393C49;
        box-sizing: border-box;
        border-radius: 8px;
        margin-right: 10px;
        cursor: grab;
        position: relative;
        &::after{
            content: '';
            position: absolute;
            width: 20px;
            height: 20px;
            top: 6px;
            right: 6px;
            background-image: url(../../assets/images/icons/checkmark-circle.svg);
            background-repeat: no-repeat;
            display: none;

        }

        svg{
            width: 25px;
            height: 25px;
            object-fit: cover;
            margin-bottom: 5px;
            fill: grey;
        }

        &-img{
            width: 25px;
            height: 25px;
            object-fit: cover;
            margin-bottom: 5px;
        }

        &__title{
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: #ABBBC2;
        }
    }
    
    .payment-method{
        .title{
            margin-top: 36px;
            margin-bottom: 16px;
        }
    }

    #credit{
        display: none;
    }

    #paypal{
        display: none;
    }

    #cash{
        display: none;
    }

    &.opened{
        width: 100%;
        position: absolute;
        padding: 64px 24px 24px 24px;
        box-sizing: border-box;
        height: 100%;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        z-index: 5;
    }

    &.opened .homepage-right{
        display: none;
    }

    .private-info{
        .private{
            display: flex;
            flex-direction: column;
            margin-bottom: 16px;

            &:last-child{
                margin-bottom: 24px;
            }

            .name-label{
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #FFFFFF;
                margin-bottom: 10px;
            }

            #name-input, #name{
                width: 360px;
                padding: 14px;
                background: #2D303E;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                outline: none;
                color: #fff;
            }


        }

        .wrapper{
            display: flex;
            align-items: center;
            padding-bottom: 26px;
            border-bottom: 1px solid #393C49;
            .left{
                display: flex;
                flex-direction: column;
                margin-right: 20px;

                .name-label{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    color: #FFFFFF;
                    margin-bottom: 10px;
                }

                #date{
                    padding: 14px;
                    width: 170px;
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    outline: none;
                    color: #fff;
                }
            }

            .right{
                display: flex;
                flex-direction: column;

                .name-label{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    color: #FFFFFF;
                    margin-bottom: 10px;
                }

                #password{
                    padding: 14px;
                    width: 170px;
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    outline: none;
                    color: #fff;

                    &::placeholder{
                        color: #fff;
                    }
                }
            }
        }

        .order-type{
            display: flex;
            margin-top: 20px;
            margin-bottom: 150px;
            .left{
                display: flex;
                flex-direction: column;
                margin-right: 15px;

                .name-label{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 130%;
                    color: #FFFFFF;
                    margin-bottom: 10px;
                }
            }

            .right{
                display: flex;
                flex-direction: column;

                .name-label{
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 130%;
                    color: #FFFFFF;
                    margin-bottom: 10px;
                }

                #name{
                    padding: 14px;
                    width: 170px;
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    outline: none;
                    color: #fff;
                }
            }
        }

        .buttons{
            display: flex;
            align-items: center;

            .cancel{
                border: none;
                border: 1px solid #EA7C69;
                box-sizing: border-box;
                border-radius: 8px;
                padding: 14px 67px;
                background: #1F1D2B;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #EA7C69;
                margin-right: 20px;
                outline: none;
                cursor: grab;
            }

            .confirm{
                background: #EA7C69;
                box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.4);
                border-radius: 8px;
                padding: 14px 34px;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #FAFAFA;
                border: none;
                outline: none;
                cursor: grab;
            }
        }
    }
}