.price-cards{
    width: 180px;
    padding: 16px;
    box-sizing: border-box;
    background: #1F1D2B;
    border-radius: 8px;

    &:nth-child(2) .status-title{
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 140%;
        color: #FF7CA3;
    }

    &-top{
        display: flex;
        align-items: center;
        margin-bottom: 8px;

        .img-wrapper{
            padding: 8px;
            box-sizing: border-box;
            background: #252836;
            border-radius: 8px;
            margin-right: 12px;
            display: flex;
            justify-content: center;
            align-items: center;
            
            img{
                width: 28px;
                // object-fit: cover;
                height: 25px;
            }
        }

        .status-title{
            font-style: normal;
            font-weight: 500;
            font-size: 12px;
            line-height: 140%;
            color: #50D1AA;
            margin-right: 8px;

            // &:nth-child(2){
            //     font-style: normal;
            //     font-weight: 500;
            //     font-size: 12px;
            //     line-height: 140%;
            //     color: #FF7CA3;
            // }
        }

        .resultImg{
            width: 20px;
            height: 20px;
            object-fit: cover;
        }
    }

    &-center{
        margin-bottom: 8px;
        .price-info{
            font-style: normal;
            font-weight: 600;
            font-size: 26px;
            line-height: 140%;
            color: #FFFFFF;
        }
    }

    &-bottom{
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #ABBBC2;
    }
}