body{
    margin: 0;
    padding: 0;
    background-color: #252836;
    font-family: 'Barlow', sans-serif;
}

h1,h2,h3,h4,h5,h6,p,a{
    margin: 0;
    padding: 0;
    text-decoration: none;
}

.main-sidebar{
    height: 100vh;
    width: 80px;
    background: #1F1D2B;
    border-radius: 16px;
    box-sizing: border-box;
    margin-right: 25px;
    position: fixed;
}

.app{
    display: flex;
}

.changer{
    margin-left: 100px;
    width: 100%;
}