.edit-block-main{
    background: #1F1D2B;
    border: 1px solid #393C49;
    border-radius: 8px;
    width: 221px;
    height: 299px;
    overflow: hidden;
    box-sizing: border-box;
    margin-bottom: 16px;

    // &n{
    //     padding: 22px;
    // }

    &__img{
        width: 143,94px;
        height: 130,62px;
        border-radius: 50%;
        margin: 0  auto;
        display: block;
        margin-bottom: 15px;
        padding-top: 20px;
    }

    &__title{
        font-style: normal;
        width: 140px;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        text-align: center;
        margin: 0 auto;
        color: #FFFFFF;
        margin-bottom: 10px;
    }

    .flexbox{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 16px;

        .edit-block-main__price, .edit-block-main__count{
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            color: #ABBBC2;
            margin-right: 20px;
        }

        .edit-block-main__count{
            margin-right: 0;
        }
    }

    .bottom{
        display: flex;
        justify-content: center;
        align-items: center;
        background: #50343A;
        opacity: 0.94;
        border-radius: 0px 0px 8px 8px;
        padding: 16px 0;
        width: 100%;
        cursor: pointer;

        .edit-img{
            position: relative;
            margin-right: 10px;
        }

        .smth{
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #EA7C69;
        }
    }
}