.block-settings{
    display: flex;
    align-items: flex-start;
    margin-bottom: 48px;

    .block-img{
        width: 20px;
        height: 20px;
        margin-right: 10px;
    }

    .block-title{
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        color: #FFFFFF;
        margin-bottom: 7px;
    }

    .block-text{
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 140%;
        color: #ABBBC2;
    }
}