.food-list{
    display: flex;
    align-items: center;
    margin-bottom: 22px;

    &__img{
        width: 61,07px;
        height: 56px;
        object-fit: cover;
        border-radius: 50%;
        margin-right: 18px;
    }

    &-right{
        .food-list__title{
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 130%; 
            color: #E0E6E9;
            margin-bottom: 8px;
        }

        .ordered{
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 140%;
            color: #ABBBC2;
        }
    }
}