.dish-card{
    width: 222px;
    background: #1F1D2B;
    border-radius: 16px;
    padding: 0 21px 24px 21px;
    box-sizing: border-box;
    cursor: grab;
    margin-bottom: 100px;
    // margin-right: 5px;

    &:nth-child(13) {
        margin-bottom: 0;
    }

    &:nth-child(12) {
        margin-bottom: 0;
    }

    &:nth-child(11) {
        margin-bottom: 0;
    }

    &:nth-child(14) {
        margin-bottom: 0;
    }

    &:nth-child(15) {
        margin-bottom: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }

    .img-holder{
        text-align: center;
    }

    &__img{
        width: 150px;
        height: 150px;
        border-radius: 50%;
        margin-top: -50%;
        margin: -50% auto 15px auto;
    }
    &__title{
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        text-align: center;
        color: #FFFFFF;
        width: 120px;
        margin: 0 auto;
        margin-bottom: 12px;
    }

    &__price{
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: #fff;
        margin-bottom: 8px;
    }

    &__subtitle{
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: #ABBBC2;
    }
}